import React from "react";
import "./CurrentWeather.scss";
import arrow from "../img/arrow-up.png";

const CurrentWeather = ({
  time,
  temperature,
  weathercode,
  windspeed,
  winddirection,
}) => {
  let getWeather = (weathercode) => {
    if (weathercode === 0) {
      return "";
    }
    if (weathercode >= 1 && weathercode <= 3) {
      return "";
    }
    if (weathercode >= 45 && weathercode <= 48) {
      return "";
    }
    if (weathercode >= 51 && weathercode <= 55) {
      return "";
    }
    if (weathercode >= 56 && weathercode <= 57) {
      return "";
    }
    if (weathercode >= 61 && weathercode <= 65) {
      return "";
    }
    if (weathercode >= 66 && weathercode <= 67) {
      return " ";
    }
    if (weathercode >= 71 && weathercode <= 75) {
      return "";
    }
    if (weathercode === 77) {
      return " ";
    }
    if (weathercode >= 80 && weathercode <= 82) {
      return "";
    }
    if (weathercode >= 85 && weathercode <= 86) {
      return "  ";
    }
    if (weathercode === 95) {
      return "";
    }
    if (weathercode >= 96 && weathercode <= 99) {
      return "  ";
    }
    return "Error";
  };

  let weather = getWeather(weathercode);

  return (
    <div className="weather__container">
      <p>: {time}</p>
      <p>: {temperature} C&#176;</p>
      <p>: {weather}</p>
      <p> : {windspeed} /</p>
      <p> : {winddirection} <img style={{transform: `rotate(${180 + winddirection}deg)`}} src={arrow} alt="" /></p>
    </div>
  );
};

export default CurrentWeather;
